import React from 'react';
import ReactDOM from 'react-dom';
import mobx, {observer} from 'mobx';
import {observable} from 'mobx-react';


@observer
export default class Home extends React.Component{

    @observable obj = {
        name: 'Espen'
    }

    render(){
        return (
            <h2>Hello {obj.name}</h2>
        )
    }
}


